import React, { Component } from 'react'
import style from 'styled-components'
import {Link} from 'react-router-dom'

const Loginbox=style.div`
border:solid black 1px;  
width:400px;
margin-top:7em;
color:white;
background-color:brown;


`

export default class Login extends Component {

    constructor(props) {
        super(props);
        this.state = { username: '',
                        Password:''};
      }
      myChangeHandler = (event) => {
        this.setState({username: event.target.value,
                        Password:event.target.vale });
      }
 
      



    render() {
        return (
            <div>
                <Loginbox className="container-fluid">
                <form>
               
                    <h1 className="mb-4">Login</h1>

                     
                <button className="btn bg-Success mt-3 ml-5 mr-5 mb-3" type="click">Admin</button>
                <button className="btn bg-Success mt-3 ml-5 mr-5 mb-3" type="click">user</button>


                    <p>Username :</p>
                    <input
                    className="form-control"
                    type='text'
                    placeholder='username'
                    onChange={this.myChangeHandler}
                    />
                    <small>haven't signup !</small>
                    <Link to="/Signup"><small> Signup Here</small></Link>
                    <p>Password :</p>
                    <input
                    className="form-control"
                    type='text'
                    placeholder='Password'
                    onChange={this.myChangeHandler}
                    />
                    <br/>
                   <button className="btn bg-Success mt-3" type="Submit">Login</button>
                </form>

                <h1>{this.state.username}</h1>
                <h1>{this.state.Password}</h1>
                </Loginbox>
                
            </div>
        )
    }
}
